/*Pixels to rems*/ /* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Inter", sans-serif;
  background: var(--Almost-White);
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --Almost-White: hsl(0, 0%, 98%);
  --Medium-Gray: hsl(0, 0%, 41%);
  --Almost-Black: hsl(0, 0%, 8%);
}

body {
  font-family: "Epilogue", sans-serif;
}

@media (max-width: 50em) {
  .overlay {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    background: linear-gradient(var(--Medium-Gray), transparent);
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  margin: 2rem 1.875rem 0 2.5rem;
}
.nav__wrapper {
  display: flex;
}
.nav__menu {
  position: relative;
}
.nav__accordion {
  margin-left: 3.4375rem;
  font-size: 0.8125rem;
  color: var(--Medium-Gray);
  background: transparent;
  border: none;
  position: relative;
  cursor: pointer;
}
.nav__accordion img {
  position: absolute;
  top: 7px;
  right: -17px;
}
.nav__options {
  background: #fff;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.1);
  width: 8.75rem;
  border-radius: 0.625rem;
  padding: 0.625rem;
}
.nav__option {
  display: flex;
  margin-top: 1.25rem;
}
.nav__option p {
  margin-left: 0.875rem;
  justify-content: flex-end;
  font-size: 0.8125rem;
}
.nav__login {
  margin-right: 2.1875rem;
}
.nav__register {
  border: 2px solid var(--Medium-Gray) !important;
  padding: 0.5rem 1.25rem;
  border-radius: 0.9375rem;
}
.nav__hamburger {
  background: url("/images/icon-menu.svg") center center no-repeat;
  width: 1.625rem;
  height: 1.625rem;
}

.btn {
  font-size: 0.8125rem;
  color: var(--Medium-Gray);
  background: transparent;
  border: none;
  cursor: pointer;
}
.btn a {
  text-decoration: none;
  font-size: 0.8125rem;
  color: var(--Medium-Gray);
}

.hidden {
  display: none;
}

#one {
  position: absolute;
  top: 100%;
  left: 0%;
}

#two {
  position: absolute;
  top: 100%;
  left: 38%;
}

@media (max-width: 50em) {
  .hide-for-tablet {
    display: none;
    pointer-events: none;
  }
}

@media (min-width: 50em) {
  .hide-for-desktop {
    display: none;
    pointer-events: none;
  }
}

@media (max-width: 50em) {
  .hide-for-mobile {
    display: none;
    pointer-events: none;
  }
}

.hamburger__menu {
  position: absolute;
  top: 0;
  right: 0;
  background-color: var(--Almost-White);
  margin-left: 30%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: 100vh;
  width: 245px;
  z-index: 5;
  opacity: 0;
}
.hamburger__hamburger {
  background: url("/images/icon-close-menu.svg") center center no-repeat;
  width: 1.625rem;
  height: 1.625rem;
  padding: 40px;
}
.hamburger__links {
  display: flex;
  margin-right: 6.25rem;
  flex-direction: column;
}
.hamburger__accordion {
  background: transparent;
  border: none;
  position: relative;
  margin-bottom: 1.25rem;
  width: 140px;
}
.hamburger__accordion img {
  position: absolute;
  top: 10px;
  right: 0;
}
.hamburger__options {
  padding-left: 1.5625rem;
}
.hamburger__option {
  display: flex;
  margin-top: 0.625rem;
}
.hamburger__option:last-child {
  margin-bottom: 0.625rem;
}
.hamburger__option p {
  padding-left: 0.875rem;
  justify-content: flex-end;
  font-size: 0.8125rem;
}
.hamburger__log {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 1.25rem;
}
.hamburger__login {
  width: 100%;
  margin-bottom: 1.25rem;
}
.hamburger__register {
  width: 80%;
  border: 2px solid var(--Medium-Gray) !important;
  padding: 0.5rem 1.25rem;
  border-radius: 0.9375rem;
}

.hide {
  opacity: 1;
}

.hero {
  display: grid;
  grid-template-columns: 1fr;
  height: 100%;
}
@media (min-width: 50em) {
  .hero {
    grid-template-columns: 1fr 1fr;
  }
}
.hero__image {
  background: url("../images/image-hero-mobile.png") center center/cover;
  min-width: 300px;
  min-height: 300px;
  margin: 1.875rem 0 3.125rem 0;
}
@media (min-width: 50em) {
  .hero__image {
    grid-column: 2/3;
    grid-row: span 2;
    background: url("../images/image-hero-desktop.png") center center/cover;
    margin-right: 8.125rem;
  }
}
.hero__main {
  text-align: center;
}
@media (min-width: 50em) {
  .hero__main {
    grid-column: 1/2;
    grid-row: 1/3;
    text-align: left;
    margin: 8.125rem;
  }
}
.hero__title {
  font-size: 1.875rem;
  font-size: clamp(1.875rem, 1.1707746479rem + 3.0046948357vw, 3.875rem);
  color: var(--Almost-Black);
  margin-bottom: 2.1875rem;
}
.hero__text {
  color: var(--Medium-Gray);
  font-size: 0.9375rem;
  margin-bottom: 2.1875rem;
}
.hero__button {
  font-size: 0.9375rem;
  font-weight: bold;
  background: var(--Almost-Black);
  border: none;
  border-radius: 0.9375rem;
  padding: 1.0625rem 1.5625rem;
  color: var(--Almost-White);
  letter-spacing: 1px;
}
.hero__icons {
  margin-top: 3.75rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
@media (min-width: 50em) {
  .hero__icons {
    grid-column: 1/2;
    grid-row: 1/3;
    text-align: left;
    margin-top: 8.125rem;
  }
}
.hero__icon {
  width: 15%;
}